* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

ul, li, h2, h4, p {
  text-decoration: none;
  margin: 0px;
  padding: 0px;
}

.list__country {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 15px;
  background-color: #d4db8f;
}


.country-list {
  margin-left: 20px;
  padding: 25px;
}

.info__item-country {
  display: block;
  height: 250px;
  padding: 25px;
  margin-left: 10px;
}

.img_item {
  width: 60px;
  height: 40px;
  display: block;
}


.country-info {
  background-color: #b2eeef;
}
